export function handleSearch(movie){
    console.log('handle search');
    const url =`https://www.omdbapi.com/?apikey=ba1534f1&s=${movie}`;
    return function(dispatch){
        fetch(url)
        .then(response=>response.json())
        .then((movies)=>{
            console.log(movies.search);
            dispatch(addSearchResults(movies.Search))
        });
    }
}

export function addSearchResults(movies){
    return {
        type: ADD_SEARCH_RESULT,
        movies
    }
}

export function addSearchTitle(title){
    return {
        type:ADD_SEARCH_TITLE,
        title
    }
}

export function handleMovieDetails(movie){
    const url = `https://www.omdbapi.com/?apikey=ba1534f1&t=${movie}`;
    return function(dispatch){
        fetch(url)
        .then(response=>response.json())
        .then(movie=>{
            dispatch(handleMovieVideo(movie.imdbID));
            dispatch(addMovieDetails(movie));
        });
    }
}
