export function handleSearchMovieDetail(id,type){
    const url = `https://api.themoviedb.org/3/find/${id}?api_key=df5924b75b0bf99fda95e6b32ee61a83&external_source=imdb_id`;
    return function(dispatch){
        fetch(url)
        .then(response=>response.json())
        .then(data=>{
            if(type=='movie'){
            console.log(data.movie_results[0]);
            dispatch(addMovieBack(data.movie_results[0].backdrop_path));
           
            dispatch(handleMoreMovies(data.movie_results[0].original_language,data.movie_results[0].genre_ids[0]));
            }
            if(type=='series'){
                dispatch(addMovieBack(data.tv_results[0].backdrop_path));
               
                dispatch(handleMoreTvs(data.tv_results[0].original_language,data.tv_results[0].genre_ids[0]));
            }
        })
    }
}

